import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import sortBy from 'lodash.sortby';
import { SEO, Layout, Image, Spinner } from '../components';
import { FancyButtonStyles } from '../components/FancyButton';
import { Release } from '../../../src/shared/types/github-api.js';

function isMacintosh() {
  return navigator.platform.indexOf('Mac') > -1;
}

function isWindows() {
  return navigator.platform.indexOf('Win') > -1;
}

const MacAsset: DownloadAsset = {
  os: 'mac',
  extension: 'dmg',
};

const WinAsset: DownloadAsset = {
  os: 'win',
  extension: 'exe',
};

const OtherAsset: DownloadAsset = {
  os: 'linux',
  extension: 'zip',
};

type DownloadAsset = {
  os: 'mac' | 'win' | 'linux';
  extension: 'dmg' | 'exe' | 'zip';
};

const useLatestVersionLink = (): string | void => {
  const [downloadLink, setDownloadLink] = useState(undefined);

  useEffect(() => {
    fetch('https://api.github.com/repos/ahmedlhanafy/cheesy-timer/releases')
      .then(res => res.json())
      .then((releases: Release[]) => {
        const [lastRelease] = sortBy(releases, ['published_at']).reverse();
        const downloadAsset: DownloadAsset = isMacintosh()
          ? MacAsset
          : isWindows()
          ? WinAsset
          : OtherAsset;

        const {
          browser_download_url: releaseDownloadLink,
        } = lastRelease.assets.find(({ name }) =>
          name.includes(downloadAsset.extension),
        );

        setDownloadLink(releaseDownloadLink || lastRelease.html_url);
      });
  }, []);

  return downloadLink;
};

const IndexPage = () => {
  const downloadLink = useLatestVersionLink();
  return (
    <Layout>
      <SEO title="Home" keywords={[`Track your time with ease`]} />
      <Title>Cheesy Timer ðŸš€</Title>
      <Desc>Cheesy timer is really awesome, it lets you track your time.</Desc>
      <Image />
      {downloadLink ? (
        <Link href={downloadLink}>Download Now</Link>
        ) : (
          <Spinner />
      )}
    </Layout>
  );
};

const Title = styled.h1`
  font-weight: bold;
  font-size: 28px;
  align-self: center;
  margin: 36px 0px;
  color: ${props => props.theme.primaryTextColor};
`;

const Desc = styled.h2`
  font-weight: 300;
  font-size: 20px;
  align-self: center;
  text-align: center;
  margin: 0px;
  color: ${props => props.theme.primaryTextColor};
`;

const Link = styled.a`
  ${FancyButtonStyles}
  margin-bottom: 16px;
`;

export default IndexPage;
